.header-color{
  /*import background color from uigradients.com */
  background: #2c3e50;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #3498db, #2c3e50);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #3498db, #2c3e50); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.landing-grid{
    background: url('../src/cup-desk-drink-434337.jpg');
    background-size:contain;
    text-align: center;
}

.avatar-image{
  height:13%;
  padding:1% 44%;
}

.banner-text{
  background-color: black;
  opacity: .4;
  width:75%;
  margin:auto;
  border-radius:10px;
}

.banner-text h1{
  font-size: 35px;
  font-weight:bold;
  color:white;
  text-align: center;
}

.banner-text hr{
  border-top: 5px dotted white;
  width:50%;
  margin:auto;
}

.banner-text p{
  color:white;
  font-size: 20px;
  padding:1em;
  text-align: center;
}

.social-links{

  justify-content: space-between;
  text-align: center;
  color:white;
  margin:auto;
}

.social-links i{
  color:white;
  font-size: 5em;

  padding: 1%;

}

.cardtitle-1{
  color:black;
  height:176px;
  background:url(../src/cup-desk-drink-434337.jpg);
}

.projects-grid{
  display: flex;
}

/* contact page css*/

.contact-body{
  margin:auto;
  position: fixed;
  width: 100%;
  height:100%;
  justify-content:center;
  padding-top:2em;
}

.contact-grid{
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  margin-bottom: 20px;
}

.contact-grid h2{
  font-family: 'Anton', sans-serif;
}

.contact-grid p{
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}

.contact-grid hr{
  border-top: 5px dotted black;
  width: 50%;
  margin:auto;
}

.contact-list i{
  font-size: 50px;
  padding-right: 5rem;
}

/* Resume page css*/

.resume-right{
  background: #27221F;
  color: white;
  padding: 2em;
}
